import {ActiveSiteSettings} from "./activeSiteSettings";

export const THEME = {...ActiveSiteSettings.THEME.THEME};
export const SECRETKEY = ActiveSiteSettings.secretKey;
export const IDGroupVK = ActiveSiteSettings.idGroupVK;
export const BUTTONTHEME = {...ActiveSiteSettings.THEME.BUTTONTHEME};

const applyObjectPatch = (target, patch) => {
    if(!patch || typeof patch !== 'object'){
        return;
    }
    Object.keys(patch).forEach((key) => {
        target[key] = patch[key];
    });
};

export const applyServerThemePatch = ({theme, buttonTheme} = {}) => {
    applyObjectPatch(THEME, theme);
    applyObjectPatch(BUTTONTHEME, buttonTheme);
};
