const isVideoSrc = (src) => {
    if (!src) {
        return false;
    }
    const clean = String(src).split("?")[0];
    const ext = clean.split(".").pop().toLowerCase();
    return ["mp4", "webm", "ogv", "ogg", "mov", "m4v"].includes(ext);
};

let activePreviewVideo = null;

const canUseHover = () => {
    return Platform.OS === "web" && typeof window !== "undefined" && window.matchMedia && window.matchMedia("(hover:hover)").matches;
};

const ProductPreviewMedia = ({imgSrc, videoSrc, imageStyle, wrapStyle, videoStyle, forcePlay = false}) => {
    const videoRef = useRef(null);
    const hoverEnabledRef = useRef(false);
    const [isPlaying, setIsPlaying] = useState(false);
    const hasVideo = Platform.OS === "web" && isVideoSrc(videoSrc);

    useEffect(() => {
        if (Platform.OS !== "web" || typeof window === "undefined" || !window.matchMedia) {
            return;
        }
        hoverEnabledRef.current = window.matchMedia("(hover:hover)").matches;
    }, []);

    useEffect(() => {
        return () => {
            const video = videoRef.current;
            if (!video) {
                return;
            }
            try {
                video.pause();
            } catch (e) {}
            if (activePreviewVideo === video) {
                activePreviewVideo = null;
            }
        };
    }, []);

    const playVideo = () => {
        const video = videoRef.current;
        if (!video) {
            return;
        }
        if (activePreviewVideo && activePreviewVideo !== video) {
            try {
                activePreviewVideo.pause();
            } catch (e) {}
        }
        if (!video.getAttribute("src")) {
            const lazySrc = video.getAttribute("data-src");
            if (lazySrc) {
                video.setAttribute("src", lazySrc);
            }
        }
        video.muted = true;
        video.playsInline = true;
        video.setAttribute("playsinline", "");
        video.setAttribute("webkit-playsinline", "");
        try {
            video.currentTime = 0;
        } catch (e) {}
        try {
            video.load();
        } catch (e) {}
        const p = video.play();
        if (p && p.then) {
            p.then(() => {
                activePreviewVideo = video;
                setIsPlaying(true);
            }).catch(() => {
                if (activePreviewVideo === video) {
                    activePreviewVideo = null;
                }
                setIsPlaying(false);
            });
            return;
        }
        activePreviewVideo = video;
        setIsPlaying(true);
    };

    const stopVideo = () => {
        const video = videoRef.current;
        if (!video) {
            return;
        }
        try {
            video.pause();
        } catch (e) {}
        if (activePreviewVideo === video) {
            activePreviewVideo = null;
        }
        setIsPlaying(false);
    };

    const onMouseEnter = () => {
        if (!hoverEnabledRef.current) {
            return;
        }
        playVideo();
    };

    const onMouseLeave = () => {
        if (!hoverEnabledRef.current) {
            return;
        }
        stopVideo();
    };

    const onPointerDown = (event) => {
        if (hoverEnabledRef.current) {
            return;
        }
        const pointerType = event?.nativeEvent?.pointerType;
        if (pointerType && pointerType !== "touch") {
            return;
        }
        playVideo();
    };

    const onTouchStart = () => {
        if (hoverEnabledRef.current) {
            return;
        }
        playVideo();
    };

    useEffect(() => {
        if (!hasVideo) {
            return;
        }
        if (forcePlay) {
            playVideo();
        } else {
            stopVideo();
        }
    }, [forcePlay, hasVideo]);

    if (!hasVideo) {
        return <Image style={imageStyle} source={{ uri: imgSrc }}/>;
    }

    const videoInlineStyle = StyleSheet.flatten([
        videoStyle,
        isPlaying ? styles.imageVideoVisible : styles.imageVideoHidden
    ]);

    return (
        <View
            style={wrapStyle}
            onMouseEnter={onMouseEnter}
            onMouseLeave={onMouseLeave}
            onPointerDown={onPointerDown}
            onTouchStart={onTouchStart}
        >
            <Image style={imageStyle} source={{ uri: imgSrc }}/>
            <video
                ref={videoRef}
                data-src={videoSrc}
                poster={imgSrc}
                muted
                playsInline
                preload="metadata"
                autoPlay={false}
                loop
                controls={false}
                disablePictureInPicture
                controlsList="nodownload noplaybackrate noremoteplayback"
                draggable={false}
                onDragStart={(event) => event.preventDefault()}
                style={videoInlineStyle}
            />
        </View>
    );
};

export const OneProduct = ({ product, onOpen, onOpenProduct }) => {

    const idUserVK = useSelector(state => state.post.idUserVK);
    const TypeDeliverySystem = useSelector(state => state.post.typeDeliverySystem);
    const DisplayAll = useSelector(state => state.post.displayAll);
    const DisplayAllAffiate = useSelector(state => state.post.displayAllAffiate);
    let ShowInBasket = true;
    const previewVideo = product?.video_preview || product?.videoPreview || "";
    const [isCardHovered, setIsCardHovered] = useState(false);
    const hasPreviewVideo = Platform.OS === "web" && isVideoSrc(previewVideo);

    let TextButtonInCart = ' ';
    let ButtonModify = false;
    if(product?.typeProductModify === true){
        TextButtonInCart = '';
        ButtonModify = true;
        if(product.buttonModifyOpenModal !== ''){
            TextButtonInCart = product.buttonModifyOpenModal;
        }
    }

    let linkURL = 'https://vk.com/app'+idUserVK.appId;
    if(idUserVK?.groupId !== null){
        linkURL += '_-'+idUserVK.groupId;
    }

    // if(TypeDeliverySystem === true) {
    //     // 
    //     DisplayAll === true ?
    //         ShowInBasket = false : ShowInBasket = true
    // }else {
    //     // 
    //     DisplayAllAffiate === true ?
    //         ShowInBasket = false : ShowInBasket = true
    // }


    const handleCardMouseEnter = () => {
        if (canUseHover()) {
            setIsCardHovered(true);
            return;
        }
        if (hasPreviewVideo) {
            setIsCardHovered(true);
        }
    };

    const handleCardMouseLeave = () => {
        setIsCardHovered(false);
    };


    return (
        <TouchableOpacity
            // style={Dimensions.get('window').width <= 420 ? {width:"100%"}:{width:"50%"} }
            style={getTypeDevice(Dimensions.get('window').width,Dimensions.get('window').height) === false ? {width:"48%",marginHorizontal:"1%"} : {width:"100%"}}
            activeOpacity={0.7}
            onPress={() => {onOpen(product);onOpenProduct(product)}}>
            <View
                style={styles.productBlock}
                onMouseEnter={handleCardMouseEnter}
                onMouseLeave={handleCardMouseLeave}
                onTouchStart={handleCardMouseEnter}
            >
                    <View style={ styles.textWrap }>
                        <View style={styles.TextImageProductL}>
                            <a style={{textDecoration:"none",color:"inherit"}} onClick={(e)=>{e.preventDefault();}} href={linkURL+"#getPageProduct&idCategory="+product.id_Category+"&idProduct="+product.id}>
                                <ProductPreviewMedia
                                    imgSrc={product.img}
                                    videoSrc={previewVideo}
                                    imageStyle={styles.image}
                                    wrapStyle={styles.mediaWrap}
                                    videoStyle={styles.imageVideo}
                                    forcePlay={isCardHovered}
                                />
                            </a>
                        </View>
                        <View style={styles.TextBlockProduct}>
                            <View style={styles.BlockTitle}>
                                <Text style={styles.title}>
                                    {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                                <Text style={styles.weight}>{product.weight ? NumberFormatPrice(product.weight,0,'.',' ')+' '+product.weight_type : false}</Text>
                            </View>
                            {product.description !== '' ? <View style={styles.ViewDescription}>
                                <Text style={styles.description}>
                                    {product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim().length > 121 ? product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim().substring(0, 121)+'...' : product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                            </View> : <View style={styles.ViewDescription}><Text></Text></View>}
                            {product.type_info !== '' ? <View style={styles.ViewTypeInfo}>
                                <Text style={styles.TypeInfo}>
                                    {/*{console.log('     ',product)}*/}
                                    {product?.type_info?.trim()}
                                </Text>
                            </View> : false}


                                    <View style={styles.ViewPrice}>
                                        <Price ProductData={product}/>
                                        {
                                            ShowInBasket === true ?
                                                ButtonModify ?
                                                    <ButtonOpenProductModify Product={product} titleButton={TextButtonInCart}
                                                                             onOpen={onOpen} onOpenProduct={onOpenProduct}/>
                                                    :
                                                    <ButtonInCart Product={product}/>

                                                : <ButtonEbee
                                                    // onPress={()=>{
                                                    //     navigation.navigate('AdressList', {
                                                    //         productId: 1,
                                                    //         title: " ",
                                                    //         booked: false,
                                                    //         productData:[{id:1,title:" ",booked:false}]
                                                    //     });
                                                    // }}
                                                    size="l" borderRadius={10} shadow={false} style={{minWidth:90,paddingTop:6,paddingBottom:7,paddingHorizontal:2,backgroundColor:"#dddddd"}}> </ButtonEbee>
                                        }
                                    </View>
                        </View>
                        <a style={{textDecoration:"none",color:"inherit"}} onClick={(e)=>{e.preventDefault();}} href={linkURL+"#getPageProduct&idCategory="+product.id_Category+"&idProduct="+product.id}>

                        <ModyficateBlock ModyficateProduct={product.modyficate} idProduct={product.id}/>
                        </a>
                    </View>
            </View>
        </TouchableOpacity>
    )
};


export class OneProductPure extends React.PureComponent{
    state = {
        isCardHovered: false
    };

    render(){

        const product = this.props.product;
        const onOpenProduct = this.props.onOpenProduct;
        const onOpen = this.props.onOpen;
        const previewVideo = product?.video_preview || product?.videoPreview || "";
        const isCardHovered = this.state.isCardHovered;
        const hasPreviewVideo = Platform.OS === "web" && isVideoSrc(previewVideo);

        const handleCardMouseEnter = () => {
            if (canUseHover()) {
                this.setState({isCardHovered: true});
                return;
            }
            if (hasPreviewVideo) {
                this.setState({isCardHovered: true});
            }
        };

        const handleCardMouseLeave = () => { this.setState({isCardHovered: false}); };

        return(
            <TouchableOpacity
                activeOpacity={0.7}
                onPress={() => {onOpen(product);onOpenProduct(product)}}
            >
                <View
                    style={styles.productBlock}
                    onMouseEnter={handleCardMouseEnter}
                    onMouseLeave={handleCardMouseLeave}
                    onTouchStart={handleCardMouseEnter}
                >
                    <View style={styles.textWrap}>
                        <ProductPreviewMedia
                            imgSrc={product.img}
                            videoSrc={previewVideo}
                            imageStyle={styles.image}
                            wrapStyle={styles.mediaWrap}
                            videoStyle={styles.imageVideo}
                            forcePlay={isCardHovered}
                        />
                        <View style={styles.TextBlockProduct}>
                            <View style={styles.BlockTitle}>
                                <Text style={styles.title}>
                                    {product.title.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                                <Text style={styles.weight}>{product.weight ? NumberFormatPrice(product.weight,0,'.',' ')+' '+product.weight_type : false}</Text>
                            </View>
                            {product.description !== '' ? <View style={styles.ViewDescription}>
                                <Text style={styles.description}>
                                    {product.description.split('&amp;quot;').join('"').split('&quot;').join('"').trim()}
                                </Text>
                            </View> : <Text></Text>}
                            {product.type_info !== '' ? <View style={styles.ViewTypeInfo}>
                                <Text style={styles.TypeInfo}>
                                    {product?.type_info?.trim()}
                                </Text>
                            </View> : false}
                            <View style={styles.ViewPrice}>
                                <Price ProductData={product}/>
                                <ButtonInCart Product={product}/>
                            </View>
                        </View>
                        <ModyficateBlock ModyficateProduct={product.modyficate} idProduct={product.id}/>
                    </View>
                </View>
            </TouchableOpacity>
        )
    }
}

