import axios from "axios";
import {ActiveSiteSettings} from "./activeSiteSettings";
import {API_KEY} from "./store/types";
import {applyServerThemePatch} from "./theme";

let currentThemeUpdatedAt = 0;

const getApiUrl = () => {
    const siteUrl = (ActiveSiteSettings && ActiveSiteSettings.URL) ? ActiveSiteSettings.URL : '';
    if(!siteUrl){
        return null;
    }
    const normalizedSite = siteUrl.endsWith('/') ? siteUrl : siteUrl + '/';
    return normalizedSite + 'api/getAppTheme&apiKey=' + API_KEY;
};

const fetchServerAppTheme = async () => {
    const url = getApiUrl();
    if(!url){
        return null;
    }
    try{
        const {data} = await axios({
            method: 'get',
            url,
            responseType: 'text'
        });
        if(data && typeof data === 'object' && !data.error){
            return {
                theme: data.theme || {},
                buttonTheme: data.buttonTheme || {},
                updatedAt: Number(data.updatedAt || 0)
            };
        }
    }catch (e){
        console.log('Server app theme load error', e);
    }
    return null;
};

const applyThemePayload = (payload) => {
    if(!payload){
        return;
    }
    applyServerThemePatch({
        theme: payload.theme || {},
        buttonTheme: payload.buttonTheme || {}
    });
    currentThemeUpdatedAt = Number(payload.updatedAt || 0);
};

export const loadServerAppTheme = async () => {
    const payload = await fetchServerAppTheme();
    if(!payload){
        return false;
    }
    applyThemePayload(payload);
    return true;
};

export const startServerAppThemePolling = ({intervalMs = 30000, onThemeChanged} = {}) => {
    let disposed = false;
    let inProgress = false;

    const tick = async () => {
        if(disposed || inProgress){
            return;
        }
        inProgress = true;
        try{
            const payload = await fetchServerAppTheme();
            if(!payload){
                return;
            }
            const nextUpdatedAt = Number(payload.updatedAt || 0);
            if(!currentThemeUpdatedAt){
                applyThemePayload(payload);
                return;
            }
            if(nextUpdatedAt > currentThemeUpdatedAt){
                applyThemePayload(payload);
                if(typeof onThemeChanged === 'function'){
                    onThemeChanged(payload);
                }
            }
        }finally {
            inProgress = false;
        }
    };

    const timerId = setInterval(tick, intervalMs);
    return () => {
        disposed = true;
        clearInterval(timerId);
    };
};
