//     shouldShowAlert: true,
//     shouldPlaySound: false,
//     shouldSetBadge: false,
//   }),
// });



export default function App() {
  const [isReady, setIsReady] = useState(false);

  useEffect(() => {
    if (Platform.OS !== 'web' || typeof window === 'undefined') {
      return;
    }

    const safeParseMessage = (raw) => {
      if (!raw || typeof raw !== 'string') {
        return null;
      }
      const trimmed = raw.trim();
      if (!trimmed || (trimmed[0] !== '{' && trimmed[0] !== '[')) {
        return null;
      }
      try {
        return JSON.parse(trimmed);
      } catch (e) {
        return null;
      }
    };

    const normalizeCityPayload = (data) => {
      if (!data || typeof data !== 'object') {
        return null;
      }
      if (data.idCity || data.name) {
        return { cityId: data.idCity, cityName: data.name };
      }
      if (data.cityId || data.cityName) {
        return { cityId: data.cityId, cityName: data.cityName };
      }
      return null;
    };

    const onFrameMessage = (event) => {
      const parsed = safeParseMessage(event?.data);
      if (!parsed?.cityLoadFrame) {
        return;
      }
      const city = normalizeCityPayload(parsed.cityLoadFrame);
      if (!city?.cityId) {
        return;
      }
      try {
        localStorage.setItem('cityData', JSON.stringify(city));
      } catch (e) {
        return;
      }
    };

    window.addEventListener('message', onFrameMessage);
    document.addEventListener('message', onFrameMessage);
    return () => {
      window.removeEventListener('message', onFrameMessage);
      document.removeEventListener('message', onFrameMessage);
    };
  }, []);

  useEffect(() => {
    if (!isReady || Platform.OS !== 'web') {
      return;
    }
    const stopPolling = startServerAppThemePolling({
      intervalMs: 30000,
      onThemeChanged: () => {
        window.location.reload();
      }
    });
    return () => {
      if (typeof stopPolling === 'function') {
        stopPolling();
      }
    };
  }, [isReady]);

  useEffect(() => {
    if (Platform.OS !== 'web' || typeof window === 'undefined' || typeof document === 'undefined') {
      return;
    }

    const getViewportHeight = () => {
      if (window.visualViewport && window.visualViewport.height) {
        return Math.round(window.visualViewport.height);
      }
      return window.innerHeight || document.documentElement?.clientHeight || 0;
    };

    const applyViewportHeight = (height) => {
      const nextHeight = height || getViewportHeight();
      if (!nextHeight) {
        return;
      }
      document.documentElement.style.height = `${nextHeight}px`;
      document.body.style.height = `${nextHeight}px`;
      const root = document.getElementById('root');
      if (root) {
        root.style.height = `${nextHeight}px`;
      }
    };

    const forceDimensionsUpdate = () => {
      if (window.visualViewport && typeof window.visualViewport.dispatchEvent === 'function') {
        window.visualViewport.dispatchEvent(new Event('resize'));
      } else {
        window.dispatchEvent(new Event('resize'));
      }
    };

    const syncViewport = () => {
      applyViewportHeight();
      forceDimensionsUpdate();
    };

    syncViewport();
    const timeoutId = setTimeout(syncViewport, 50);

    const onResize = () => applyViewportHeight();
    window.addEventListener('resize', onResize);

    const onVisualViewportResize = () => applyViewportHeight();
    if (window.visualViewport && window.visualViewport.addEventListener) {
      window.visualViewport.addEventListener('resize', onVisualViewportResize);
    }

    const onBridge = (event) => {
      if (event?.detail?.type === 'VKWebAppViewportChanged') {
        applyViewportHeight(event.detail?.data?.height);
        forceDimensionsUpdate();
      }
    };
    bridge.subscribe(onBridge);

    return () => {
      clearTimeout(timeoutId);
      window.removeEventListener('resize', onResize);
      if (window.visualViewport && window.visualViewport.removeEventListener) {
        window.visualViewport.removeEventListener('resize', onVisualViewportResize);
      }
      bridge.unsubscribe(onBridge);
    };
  }, []);


  if (!isReady) {
    return (
        <AppLoading
            startAsync={bootstrap}
            onFinish={() => setIsReady(true)}
            onError={err => console.log(err)}
        />
    )
  }

  const store = require('./src/store').default
  const AppNavigation = require('./src/navigation/AppNavigationV2').default

  return(
      <Provider store={store}>
        <AppNavigation />
      </Provider>

  )

  // return (
  //   <Provider store={store}>
  //     {/*<Text>Your expo push token: {expoPushToken}</Text>*/}
  //     <AppNavigation />
  //   </Provider>
  // )
}
